<template>
  <div>
    <Container
      title=''
      sub-title='3 '
    >
      <ki-button @click='click1'></ki-button>
    </Container>
    <Container
      title=''
      sub-title=''
    >
      <ki-button @click='click2("success")'></ki-button>
      <ki-button @click='click3("error")' style='margin: 0 20px;'></ki-button>
      <ki-button @click='click4("info")' style='margin-right: 20px;'></ki-button>
      <ki-button @click='click5("warning")'></ki-button>
    </Container>
    <Container
      title=''
      sub-title=' delay , -1 '
    >
      <ki-button @click='click6'></ki-button>
      <ki-button @click='close' style='margin: 0 20px;' type='warning' size='small'></ki-button>
      <ki-button @click='click7'>10s </ki-button>
    </Container>
    <Container
      title=' Icon'
    >
      <ki-button @click='click8("success")'></ki-button>
      <ki-button @click='click9("error")' style='margin: 0 20px;'></ki-button>
      <ki-button @click='click10("info")' style='margin-right: 20px;'></ki-button>
      <ki-button @click='click11("warning")'></ki-button>
    </Container>
  </div>
</template>

<script lang='ts'>
import { defineComponent } from 'vue';
import {
  Button, Message,
} from '@/components';
import Container from './component/container.vue';

export default defineComponent({
  name: 'MessageExample',
  components: {
    KiButton: Button,
    Container,
  },
  setup() {
    let hide: any;
    return {
      click1() {
        Message.info('');
      },
      click2(info: string) {
        Message.success(info);
      },
      click3(info: string) {
        Message.error(info);
      },
      click4(info: string) {
        Message.info(info);
      },
      click5(info: string) {
        Message.warning(info);
      },
      click6() {
        hide = Message.warning({
          content: '',
          delay: -1,
        });
      },
      close() {
        hide();
      },
      click7() {
        Message.warning({
          content: '10s ',
          delay: 10000,
        });
      },
      click8(info: string) {
        Message.success({
          content: info,
          showIcon: true,
        });
      },
      click9(info: string) {
        Message.error({
          content: info,
          showIcon: true,
        });
      },
      click10(info: string) {
        Message.info({
          content: info,
          showIcon: true,
        });
      },
      click11(info: string) {
        Message.warning({
          content: info,
          showIcon: true,
        });
      },
    };
  },
});
</script>

